import plural from 'plural-ru';

export const pluralizeNights = (values) => {
  const nights = plural(values.nightsAmount, '', '', '');
  return ` ${values.roomType}  ${values.nightsAmount} ${nights}`;
};

//  - 2 , 2   12   1   12 
//   - 2 , 2   5  12   1   5 

export const pluralizeVisitors = (values) => {
  const { adultsAmount, children5To12, childrenBelow5 } = values;
  const adultsDeclension = plural(values.adultsAmount, '', '', '');
  const children5To12Declension = plural(values.children5To12, '', '', '');
  const childrenBelow5Declension = plural(values.childrenBelow5, '', '', '');

  const adultsPlural = `${adultsAmount} ${adultsDeclension}`;
  const children5To12Plural = children5To12 > 0
    ? `, ${children5To12} ${children5To12Declension}  5  12 `
    : '';
  const childrenBelow5Plural = childrenBelow5 > 0 ? `  ${childrenBelow5} ${childrenBelow5Declension}  5 ` : '';

  return `${adultsPlural}${children5To12Plural}${childrenBelow5Plural}`;
};
