import * as yup from 'yup';

const validationSchema = yup.object().shape({
  adultsAmount: yup
    .number()
    .min(1, '   1 ')
    .required('  '),
  children5To12: yup
    .number(),
  childrenBelow5: yup
    .number()
    .test(
      'acceptedAmount',
      '  3   1 ',
      (children, { parent: { adultsAmount } }) => {
        if (adultsAmount > 0 && children > 0) {
          const acceptedRatio = 0.33;
          const currentRatio = (adultsAmount / children).toFixed(2);
          return currentRatio === Infinity
            ? true
            : currentRatio >= acceptedRatio;
        }
        return true;
      },
    ),
  nightsAmount: yup
    .number()
    .min(1, '    1 ')
    .required('  '),
  surname: yup
    .string()
    .matches(/^[^\d]*$/, '    ')
    .required('  '),
  name: yup
    .string()
    .matches(/^[^\d]*$/, '    ')
    .required('  '),
  middleName: yup
    .string()
    .matches(/^[^\d]*$/, '    '),
  phoneNumber: yup
    .string()
    .min(8, '    ')
    .required('  '),
  birthDate: yup
    .string()
    .required('  '),
});

export default validationSchema;
